import { useState } from "react";
import "./styles.css";

export default function App() {
  const [todoList, setTodoList] = useState([]);
  const [inputVal, setInputVal] = useState("");
  const addList = (todo) => {
    let arr = [...todoList];

    arr.push(todo.toString());
    setTodoList(arr);
  };

  const removeitem = (todo) => {
    var arr = [...todoList];

    arr.splice(todo, 1);

    setTodoList(arr);
  };

  return (
    <div className="App">
      <h1>Todo List </h1>

      <input
        type="text"
        id="inputItem"
        value={inputVal}
        placeholder="Add new Todo"
        onChange={(e) => {
          setInputVal(e.target.value);
        }}
      />
      <button
        onClick={() => {
          if (inputVal != "") {
            addList(inputVal);
            setInputVal("");
          } else {
            alert("Add Item in the box");
          }
        }}
        className="btn"
      >
        Add item
      </button>
      <div className="list-bkg">
        {todoList.map((text, index) => (
          <span key={index}>
            <li>
              {text}
              <button
                className="btn-del"
                onClick={() => {
                  removeitem(index);
                }}
              >
                {" "}
                Done
              </button>
            </li>
          </span>
          // <li key={index} >{text}</li>
        ))}
      </div>
    </div>
  );
}
